import store from ".";
import {
  authModule,
  repositoriesModule,
  jobsModule,
  settingsModule
} from "./modules";

store.watch(
  () => ({
    isLoggedIn: authModule.isLoggedIn
  }),
  async ({ isLoggedIn }) => {
    if (isLoggedIn) {
      await Promise.all([
        repositoriesModule.fetchAllRepositories(),
        jobsModule.fetchAllJobs(),
        settingsModule.fetchAllSettings()
      ]);
    }
  },
  { immediate: true }
);

// use a global variable as hot reloading won't clear the interval automatically
clearTimeout(window._PING_TIMEOUT);
const ping = async () => {
  await authModule.pingServer();
  window._PING_TIMEOUT = setTimeout(ping, 5000);
};
ping();
